    this.isRemainsDetailInfoShown = false;
    this.isIncomeDetailForm = false;
    this.isIncomeEditForm = false;
    this.isDateNotNullForAddNewGoalForm = false;
    this.isDateNotNullForAddNewLoanForm = false;
